.main {
  height: 100vh;
  overflow-y: auto;
}

.myLogo {
  display: flex;
  flex-direction: column;

  margin-bottom: 3.5rem;
}
.myLogo > img {
  height: 50px;
  width: 100%;
  padding: 0;
  margin: 40px 0 0;
}
.myLogo > h4 {
  margin-top: 20px;
  text-align: center;
}

.gridContainer {
  height: 100%;
  padding: 40px 80px 60px 40px;
  margin: 0px;
  background-color: white;
  display: grid;
  grid-template-columns: 20% 42% 38%;
  grid-template-rows: 10% 40% 50%;
  gap: 20px;
}
.gridContainer .gridItem {
  background-color: white;
  border-radius: 10px;
  box-shadow: 2px 2px 8px 10px #fbfbfb;
}

.item1 {
  padding: 28px;
  grid-row: span 3;
}
.item2 {
  padding: 10px;
  grid-column: span 2;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.item2 h4 {
  margin: 0;
}
.item3 {
  padding: 10px;
  grid-column: span 2;

  display: flex;
  flex-direction: column;
}

.item3 .flexRow1 {
  display: flex;
  height: 50%;
  width: 100%;
}

.item3 .flexRow2 {
  display: flex;
  height: 50%;
  width: 100%;
}

