.anchor {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  height: 80px;
  width: 100%;
  margin: 0px;
  border: 2px dashed #1cb56d;
  border-radius: 10px;
}
.a1 {
  margin-top: 38px;
  align-self: center;
}
.a2 {
  padding: 0 10px 5px 0;
  margin-top: auto;
  font-size: x-small;
  align-self: flex-end;
}

.sideItem:hover {
  background-color: lightgreen;
  color: inherit;
}

.sideItem .tabImage {
  margin: 0 16px 0px 10px;
  object-fit: contain;
  height: 80%;
  float: left;
}
.activate {
  background-color: #1cb56d;
  color: white;
}
